const app = getApp();
import helper from '../common/helper.js';
import uniPopup from '@/components/uni-popup.vue';
export default {
	data() {
		return {
			bottomData: [
				{
					text: '微信好友',
					type: 'WXSceneSession',
					icon: 'iconweixin'
				},
				{
					text: '朋友圈',
					type: 'WXSenceTimeline',
					icon: 'iconpengyouquan'
				}
			],
			codeType: ''
		}
	},
	components: {
		// wTable,
		uniPopup
	},
	onLoad(option) {
		console.log(option)
		this.codeType = option.type
		this.imgUrl = helper.imgUrl;
		if(this.codeType == '2'){
			this.getCode()
		}
	},
	// 微信分享
	onShareAppMessage() {
		let url = this.getPageUrl()
		return {
			title: this.articleDetail.title,
			path: url
		}
	},
	methods: {
		quickInlet(e) {
			if (e == 2) {
				this.$refs.showshare.open();
			} else if (e == 1) {
				this.getList()
			}
			console.log(e);
		},
		// 分享获取数据
		getCode(){
			uni.request({
				url: `${helper.requestUrl}/promote-showmycode`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					// uni.hideLoading();
					res = helper.null2str(res);
					console.log(res,'---');
					if (res.data.code == -1) {
						
						uni.showToast({
							title: res.data.tip_msg,
							icon: 'none'
						});
						setTimeout( e =>{
							uni.navigateBack({
								delta: 1
							})
						},2000)
					} else {
						
						
					}
				}
			});
		},
		// 提交申请
		getList(){
			uni.request({
				url: `${helper.requestUrl}/promote-getmycode`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					// uni.hideLoading();
					res = helper.null2str(res);
					console.log(res,'++++');
					if (res.data.code == 0) {
						uni.navigateTo({
							url: '/pages/getQrCode'
						})
					} else {
						uni.showToast({
							title: res.data.tip_msg,
							icon: 'none'
						});
						setTimeout( e =>{
							uni.navigateBack({
								delta: 1
							})
						},2000)
					}
				}
			});
		},
		cancel(type) {
			this.$refs['show' + type].close();
		},
		goShare(e) {
			console.log(e)
			let sceneType = ''
			if(e == 'WXSceneSession'){
				sceneType = 'WXSceneSession'
			} else if(e == 'WXSenceTimeline'){
				sceneType = 'WXSenceTimeline'
			}
			uni.share({
			    provider: "weixin",
			    scene: sceneType,
			    type: 0,
			    href: this.collectionList.share_link,
			    title: "新微金论坛",
			    summary: "我正在使用新微金论坛，赶紧跟我一起来体验！",
			    imageUrl: "https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/uni@2x.png",
			    success: function (res) {
			        console.log("success:" + JSON.stringify(res));
			    },
			    fail: function (err) {
			        console.log("fail:" + JSON.stringify(err));
			    }
			});
		},
	}
}
